const getCurrentZone = require("./getCurrentZone");

function rmLink() {
  $(".menu-scroll").on("click", (e) => {
    e.preventDefault();
    const target = getCurrentZone(e.target, ".dbd20-right-panel__menu-link");
    const attr = $(target).attr("href");
    const elem = $(attr);
    if ($(elem).length != 0) {
      $("body,html").animate({ scrollTop: $(elem).offset().top }, 500);
    }
  });
  $(".dbd20-right-panel__menu-link_subtoggle").on("click", function (e) {
    e.preventDefault();
    $(".dbd20-right-panel__menu-item-toggle").fadeToggle();
  });
}

function init() {
  if (document.querySelector(".dbd20-right-panel__menu")) {
    return rmLink();
  }
}
module.exports = init;
